<template>
  <div id="app">
    <h2> ChatBot in Vue.js</h2>
    <div class="chat-content"  v-if="responses && responses.length > 0">
      <div v-for="res in responses" :key="res.msg" v-bind:class="{to : res.sentBy === 'user',from : res.sentBy === 'bot'}" class="message">
      {{res.msg}}
      </div>
    </div>
    <div class="wrap">
      <input placeholder="Enter text" class="input" type="text" v-model="from" @keyup.enter="submit()"><button class="submit" v-on:click="submit()">Send</button>
    </div>
  </div>
</template>

<script>
import {ApiAiClient} from 'api-ai-javascript';
import config from './config';

class Response{
  msg = "";
  sentBy = "";
   constructor(msg,sentBy){
     this.msg = msg;
     this.sentBy = sentBy;
   }
}
const client = new ApiAiClient({accessToken: config.app.token});

export default{
data: function(){
  return{
    from:"",
    responses:[]
  }
},

methods:{
  submit(){
    this.responses.push(new Response(this.from,"user"));
    client.textRequest(this.from).then((res) => {
    this.from = "";
      setTimeout(()=>{
        this.responses.push(new Response(res.result.fulfillment.messages[0].speech,"bot"));
      },4);
    });  
  }

  
  }

}
</script>

<style>
#app {
  background-image: url('./../what-the-hex.png');
  height: 100vh;
  margin: 0px;  
  padding-top: 10px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
   margin:auto;
}
.wrap{
    padding-top: 5px;
    display: flex;
    margin: auto;
    width: 400px;
}
.input{
    background-color: #fff;
    border: 0.1rem solid #d1d1d1;
    border-radius: .4rem;
    box-shadow: none;
    box-sizing: inherit;
    height: 3.8rem;
    padding: .6rem 1.0rem;
    width: 300px;
    height: 28px; 
    margin-right: 2px;
}
.submit{
    background-color: #9b4dca;
    border: 0.1rem solid #9b4dca;
    border-radius: .4rem;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 1.1rem;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
}
.submit:hover{
    background-color: #606c76;
    border-color: #606c76;
    color: #fff;
    outline: 0;
}
.input:focus{
  border-color: #9b4dca;
  outline: 0;
}
.chat-content{
  /* background-color: #fff; */
  max-height: 600px;
  width: 400px;
  margin: auto;
  /* border-radius: 20px; */
  border: solid #9b4dca 0.1rem;
  overflow-y: auto;
  overflow-x: hidden;
}
.message {
    word-wrap: break-word;
    border-radius: 50px;
    margin: 5px 15px 10px;
    padding: 15px 20px;
    position: relative;
    font-weight: bold;
}
.message.to {
    text-align: right;
    background-color: #2095FE;
    color: #fff;
    margin-left: 100px;
    text-align: right;
}
.message.from {
  text-align: left;
    background-color: #E5E4E9;
    color: #363636;
    margin-right: 100px;

}
::-webkit-scrollbar-track
{
    border-radius: 8px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar
{
    width: 10px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    background-color: lightgray;
}
</style>



